import React, { useState } from 'react';
import styled from 'styled-components';
import Login from './Pages/Login';
import Dashboard from './Pages/Dashboard';
import Loader from './Pages/Loader';
import axios from 'axios';
import './style.css';
import { BrowserRouter as Router } from 'react-router-dom';

const App = () => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [debugMode, setDebugMode] = useState(false);
  const [loading, setLoading] = useState(false);

  const handleLogin = (employeeNumber, password, debug) => {
    console.log("login");
    setLoading(true);
    
    if (debug) {
        setTimeout(() => {
            setIsAuthenticated(true);
            setDebugMode(debug);
            setLoading(false);
        }, 3000);
    } else {
        axios
          .post(
            '${process.env.REACT_APP_API_URL}employee/login',
            {
              employeeNumber,
              password,
            },
            {
              headers: { 'X-API-KEY': '34be70f8-aef9-47bd-8f8a-674503d24e73' },
            }
          )
          .then((response) => {
            if (response.status === 200) {
              setIsAuthenticated(true);
              setDebugMode(debug);
              setTimeout(() => {
                  setLoading(false);
              }, 3000);
            }
          })
          .catch((error) => {
            console.log(error);
            setLoading(false);
          });
    }
  };

  return (
    <Router>
      <Container>
        {loading && <Loader />}
        {!isAuthenticated && (
          <Login
            isAuthenticated={isAuthenticated}
            loginHandler={handleLogin}
            debugMode={debugMode}
          />
        )}
        {isAuthenticated && (
          <Dashboard isAuthenticated={isAuthenticated} debugMode={debugMode} />
        )} 
      </Container>
    </Router>
  );
};

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  outline: 1px solid blue;
`;

export default App;
